body {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
}

.wrapper {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  min-height: 200vh;
}

.container {
  max-width: 1170px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

.rub::after {
  content: " ₽";
}

.basket__circle {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
}

.basket__image {
  font-size: 24px;
  line-height: 0;
  color: #fff;
}

.basket_header .basket__circle {
  width: 44px;
  height: 44px;
  background-color: #009baf;
}

.basket_product .basket__circle {
  width: 40px;
  height: 40px;
  background-color: #dadada;
}

.product-card {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 14px 22px;
  border: 1px solid #dadada;
  overflow: hidden;
}

.product-card__labels-block {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  cursor: default;
}

.product-card__label {
  height: 22px;
  padding: 0 16px;
  border-radius: 11px;
  font-size: 14px;
  font-weight: 900;
  line-height: 24px;
  text-transform: uppercase;
  margin-bottom: 5px;
}

.product-card__label_day {
  background-color: #1ab9ce;
}

.product-card__label_new {
  background-color: #f68038;
}

.product-card__link {
  font-size: 15px;
  margin-bottom: 25px;
}

.product-card__image {
  margin-bottom: 10px;
}

.product-card__image img {
  width: 100%;
}

.product-card__title {
  color: #009baf;
  font-weight: 900;
}

.product-card__subtitle {
  color: #009baf;
}

.product-card__old-price {
  align-self: flex-end;
  font-size: 18px;
  text-decoration-line: line-through;
  color: #999999;
  cursor: default;
}

.product-card__old-price::after {
  font-size: 12px;
}

.product-card__footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.product-card__price {
  font-size: 24px;
  font-weight: 900;
  color: #000;
  cursor: default;
}

.product-card__price::after {
  font-size: 18px;
}

.header {
  position: fixed;
  z-index: 50;
  width: 100%;
}

.header__up-block {
  height: 90px;
  background-color: #009baf;
  color: #fff;
}

.header__container {
  position: relative;
  display: flex;
  justify-content: space-between;
  height: 100%;
}

.header__menu {
  flex: 0 0 37%;
}

.header__center-element {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, 0);
  display: flex;
  align-items: flex-end;
  width: 200px;
  height: 130px;
  padding-bottom: 40px;
  border-radius: 0% 0% 50% 50%/0% 0% 45% 45%;
  background-color: #009baf;
}

.header__down-block {
  height: 70px;
  background-color: #fff;
}

.menu {
  height: 100%;
  padding: 12px 0 20px;
}

.menu__icon {
  z-index: 51;
  display: none;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 30px;
  height: 30px;
  padding: 5px 0;
  cursor: pointer;
  overflow: hidden;
}

.menu__icon.active .menu__icon-line:first-child {
  transform: translateY(9px) rotate(45deg);
}

.menu__icon.active .menu__icon-line:nth-child(2) {
  width: 0;
}

.menu__icon.active .menu__icon-line:last-child {
  transform: translateY(-9px) rotate(-45deg);
}

.menu__icon-line {
  width: 100%;
  height: 2px;
  transition: 200ms;
  background-color: #fff;
}

.navigation {
  height: 100%;
}

.navigation__list {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-content: space-between;
  flex-wrap: wrap;
  height: 100%;
}

.navigation__item {
  align-self: flex-start;
  margin-right: 5px;
  border-radius: 14px;
  transition: 200ms;
}

.navigation__link {
  padding: 5px 22px;
  font-family: Play, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
  text-decoration: underline;
}

.logo {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
  width: 100%;
}

.logo__image {
  width: 180px;
}

.logo__text {
  font-size: 12px;
  font-weight: 300;
  cursor: default;
}

.phones {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 4%;
  flex: 0 0 38%;
  padding: 9px 40px 18px 0;
}

.phones__icon {
  font-size: 35px;
  line-height: 25px;
}

.phones__line {
  width: 1px;
  height: 30px;
  background-color: #fff;
}

.phones-block__location {
  font-size: 12px;
  font-weight: 300;
  cursor: default;
}

.phones-block__number {
  font-size: 18px;
  font-weight: 900;
}

.link {
  display: flex;
  align-items: center;
  gap: 8px;
}

.link__icon {
  color: #999999;
  font-size: 22px;
  line-height: 0;
  transition: 200ms;
}

.link__text {
  color: #999999;
  font-size: 14px;
  font-weight: 400;
  transition: 200ms;
  border-bottom: 1px dashed #999999;
}

.link_email .link__icon {
  font-size: 20px;
}

.link_email .link__text {
  border-bottom: 1px solid #999999;
}

.input {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10%;
  flex: 0 0 37%;
  padding-left: 20px;
}

.interplay {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 8%;
  flex: 0 0 40%;
  padding-right: 35px;
}

.interplay__basket {
  position: relative;
}

.interplay__amount {
  position: absolute;
  top: -8px;
  left: 28px;
  border-radius: 12px;
  padding: 4px 8px;
  background-color: #f68038;
  color: #fff;
  font-size: 16px;
  line-height: 16px;
  font-weight: 700;
}

.timetable {
  display: flex;
  align-items: center;
  gap: 12px;
  cursor: default;
}

.timetable__icon {
  color: #999999;
  font-size: 28px;
  line-height: 0;
}

.timetable__text {
  color: #999999;
}

.timetable__time_bold {
  font-weight: 900;
}

.main {
  margin-top: 165px;
}

.main__container {
  display: flex;
  gap: 30px;
  overflow: hidden;
}

.main__leftside {
  flex: 0 0 260px;
}

.main__catalogbar {
  width: 100%;
  margin-bottom: 40px;
}

.main__news {
  width: 100%;
  margin-bottom: 40px;
}

.main__reviews {
  width: 100%;
}

.main__rightside {
  flex: 1 1 auto;
  min-height: 100vh;
}

.main__search {
  width: 100%;
  margin-bottom: 22px;
}

.catalogbar {
  position: relative;
  width: 100%;
  color: #fff;
  transition: 200ms;
}

.catalogbar__head {
  position: relative;
  z-index: 40;
}

.catalogbar__body {
  position: absolute;
  z-index: 39;
  bottom: 0;
}

.ctlg-head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100px;
  padding: 15px 20px;
  background-color: #009baf;
}

.ctlg-head__title {
  flex: 0 1 65%;
  font-family: UniSansHeavyCaps, sans-serif;
  font-size: 30px;
  font-weight: 400;
  cursor: default;
}

.ctlg-head__button {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 5px;
  width: 36px;
  height: 36px;
  background-color: #068292;
  cursor: pointer;
}

.ctlg-head__button.active .ctlg-head__btnline:first-child {
  transform: translateY(7px) rotate(45deg);
}

.ctlg-head__button.active .ctlg-head__btnline:nth-child(2) {
  width: 0;
}

.ctlg-head__button.active .ctlg-head__btnline:last-child {
  transform: translateY(-7px) rotate(-45deg);
}

.ctlg-head__btnline {
  width: 24px;
  height: 2px;
  background-color: #fff;
  transition: 200ms;
}

.ctlg-body {
  width: 100%;
  background-color: #009baf;
}

.ctlg-body__list {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  height: 100%;
}

.ctlg-body__link {
  width: 100%;
  height: 40px;
  padding-left: 20px;
  border-bottom: 1px solid #1ab9ce;
  font-size: 12px;
  font-weight: 300;
  line-height: 40px;
  cursor: pointer;
}

.ctlg-body__link_menu {
  position: relative;
}

.ctlg-body__link_menu::before {
  position: absolute;
  right: 10px;
  font-size: 16px;
  line-height: 40px;
  transition: 300ms;
}

.ctlg-body__panel {
  position: absolute;
  top: 0;
  z-index: 37;
  left: 100%;
}

.ctlg-panel {
  width: 100%;
  height: 100%;
  overflow: hidden;
  pointer-events: none;
}

.ctlg-panel__item {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  border: 2px solid #009baf;
  border-left: none;
  background-color: #fff;
  transform: translate(-100%, 0);
  transition: 200ms;
}

.ctlg-panel__tipes {
  padding: 12px 0 12px;
  color: #1ab9ce;
}

.ctlg-panel__link {
  width: 100%;
  height: 36px;
  padding-left: 25px;
  line-height: 36px;
}

.ctlg-panel__products {
  padding: 2px;
}

.ctlg-panel.active {
  pointer-events: auto;
  z-index: 38;
}

.ctlg-panel.active .ctlg-panel__item {
  transform: translate(0, 0);
}

.news-block__title {
  margin-bottom: 26px;
  color: #dadada;
  font-family: UniSansHeavyCaps, sans-serif;
  font-size: 30px;
  font-weight: 400;
  text-transform: uppercase;
  transition: 100ms;
}

.news-block__card {
  margin-bottom: 30px;
}

.card-news__title {
  margin-bottom: 16px;
  font-weight: 900;
  line-height: 18px;
  text-transform: uppercase;
  transition: 100ms;
}

.card-news__body {
  display: flex;
  justify-content: space-between;
  gap: 16px;
}

.card-news__date {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: 8px;
  color: #dadada;
}

.card-news__day {
  font-size: 24px;
  font-weight: 900;
  line-height: 24px;
}

.card-news__month {
  font-size: 18px;
  line-height: 12px;
}

.card-news__description {
  line-height: 18px;
  color: #999999;
}

.reviews-block__title {
  margin-bottom: 26px;
  color: #dadada;
  font-family: UniSansHeavyCaps, sans-serif;
  font-size: 30px;
  font-weight: 400;
  text-transform: uppercase;
  transition: 100ms;
}

.reviews-block__card {
  margin-bottom: 20px;
}

.card-review__head {
  display: flex;
  justify-content: space-between;
  margin-bottom: 6px;
  line-height: 18px;
}

.card-review__author {
  font-weight: 900;
  text-transform: uppercase;
  transition: 100ms;
}

.card-review__date {
  color: #999999;
  transition: 100ms;
}

.card-review__body {
  display: flex;
  gap: 10px;
  padding: 10px;
  transition: 100ms;
}

.card-review__icon {
  color: #dadada;
  font-size: 60px;
  font-weight: 900;
  line-height: 56px;
  transition: 100ms;
}

.card-review__text {
  line-height: 18px;
  color: #999999;
  transition: 100ms;
}

.search-block {
  position: relative;
  width: 100%;
  height: 40px;
}

.search-block__form {
  position: relative;
  z-index: 30;
  display: flex;
  width: 100%;
  height: 100%;
}

.search-block__options {
  flex: 0 0 20%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  background-color: #f3f3f3;
  color: #999999;
  cursor: pointer;
}

.search-block__magnifier::before {
  display: block;
  font-size: 20px;
  line-height: 18px;
}

.search-block__categories {
  position: relative;
  border-bottom: 1px dashed;
  font-size: 14px;
  font-weight: 400;
}

.search-block__arrows::before {
  display: block;
  font-size: 12px;
  line-height: 10px;
  transition: 300ms;
}

.search-block__arrows.active::before {
  transform: rotate(90deg);
}

.search-block__search {
  flex: 1 0 50%;
  border-top: 2px solid #f3f3f3;
  border-bottom: 2px solid #f3f3f3;
  background-color: #fff;
  padding: 0 12px;
  color: #999999;
  font-size: 14px;
}

.search-block__button {
  flex: 0 0 10%;
  background-color: #f68038;
  color: #fff;
  font-weight: 900;
  text-transform: uppercase;
}

.search-block__list {
  position: absolute;
  z-index: 29;
  top: 40px;
  left: 0;
  display: grid;
  grid-template-columns: repeat(3, 33%);
  width: 100%;
  padding: 22px 30px 0;
  border-bottom: 4px solid #f68038;
  background-color: #fff;
  transform: translateY(-102%);
  transition: 300ms;
}

.search-block__list.active {
  transform: translateY(0);
}

.search-block__item {
  margin-bottom: 22px;
}

.search-block__checkbox {
  display: none;
}

.search-block__checkbox:checked + .search-block__label {
  background-color: #f49e68;
  color: #fff;
}

.search-block__label {
  padding: 3px 10px;
  border-radius: 10px;
  color: #999999;
}

.search-block__label:hover {
  background-color: #f3f3f3;
}

.top-slider {
  width: 100%;
  height: 380px;
}

.top-slider__wrapper {
  width: 100%;
  height: 100%;
}

.no-webp .top-slider__slide{
  background-image: url("../img/main_slider/02.jpg"); }

.top-slider__slide {
  position: relative;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center, center;
}

.top-slider__gradient {
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  height: 100%;
  background-image: linear-gradient(to right, rgba(225, 225, 225, 0.8), transparent);
}

.top-slider__info {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.slide-info {
  display: flex;
  flex-direction: column;
  padding: 20px 36px;
  font-weight: 900;
  cursor: default;
}

.slide-info__title {
  margin-bottom: 50px;
  font-size: 48px;
  line-height: 43px;
  text-transform: uppercase;
}

.slide-info__title span {
  color: #f68038;
}

.slide-info__description {
  margin-bottom: 10px;
}

.slide-info__text {
  font-size: 14px;
  line-height: 24px;
  text-transform: uppercase;
}

.slide-info__text_style1 {
  color: #f68038;
  font-size: 18px;
}

.slide-info__text_style3 {
  padding: 5px 10px;
  color: #fff;
  background-color: #999999;
}

.slide-info__text_style4 {
  color: #999999;
}

.slide-info__price {
  align-self: center;
  margin-bottom: 20px;
  color: #fff;
  font-size: 36px;
  line-height: 30px;
}

.slide-info__button {
  position: relative;
  align-self: center;
  padding: 10px 35px 10px 18px;
  border: 3px solid #fff;
  border-radius: 20px;
  color: #fff;
  font-size: 14px;
  line-height: 9px;
  text-transform: uppercase;
  transition: 200ms;
}

.slide-info__button::before {
  position: absolute;
  top: 50%;
  right: 11px;
  transform: translateY(-55%);
  font-size: 16px;
}

@media (min-width: 992px) {
  .basket_product .basket__circle:hover {
    background-color: #f68038;
  }

  .product-card__link:hover .product-card__title {
    text-decoration: underline;
  }

  .product-card__link:hover .product-card__subtitle {
    text-decoration: underline;
  }

  .navigation__item:hover {
    background-color: #068292;
  }

  .link:hover {
    color: #1ab9ce;
  }

  .link:hover .link__icon {
    color: #1ab9ce;
  }

  .link:hover .link__text {
    color: #1ab9ce;
    border-bottom: 1px dashed #1ab9ce;
  }

  .link_email:hover .link__text {
    border-bottom: 1px solid #1ab9ce;
  }

  .ctlg-body__link:hover {
    background-color: #068292;
  }

  .ctlg-panel__link:hover {
    background-color: #71c7d3;
    color: #fff;
